<template>
  <div class="mail_v main_navs">
    <Header></Header>
    <div class="container">
      <div class="reg_main">
        <div class="card-body card_main_one">
          <div class="wrapper text-center">
            <sweetalert-icon icon="success"></sweetalert-icon>
            <h2>Muvaffaqiyatli ro'yxatdan o'toldi!</h2>
            <button class="btn btn-primary" @click="$router.push('/settings')">Profile sahifaga o'tish</button>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
export default {
  components: {Footer, Header},
  data(){
    return{
      email:'',
      password:'',
      passport_number:'',
      passport_jshshr:''
    }
  },
  mounted() {
    const form = new FormData();
    form.append("token", this.$route.query.token);
    this.$axiosDefault.post('students/verify/',form)
    .then(()=>{
      localStorage.setItem('reg_token',this.$route.query.token)
      this.$axiosDefault.get(`students/me/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language':'oz'
            }
          }
      )
    .then(res=>{
      console.log(res.data.user_type,"dkasjdna")
      if(res.data.user_type==="0"){
        this.$router.push('/settings')
      }
      if(res.data.user_type==="1"){
        this.$router.push('/teacher_settings')
      }
      if(res.data.user_type==="2") {
        this.$router.push('/others_settings')
      }

        }
    )
    })
    .catch(error=>{
      console.log(error.message)

      this.$toast(`${error.message}`, {
        timeout: 4000,
        type:"error"
      });
    })

  }

}
</script>
